body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, rgb(21, 129, 236), rgb(7, 0, 106));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.robo_container {
  width: fixed;
  padding: 10px;
  text-align: center;
  background-color: rgb(63, 185, 237);
  display: inline-block;
  margin: 15px;
  border-radius: 20px;

}

.robo_container:hover {
  background-color: rgb(67, 136, 233);
}

