.input-icon i {
  position: absolute;
}

.input-icon {
  width: 100%;
  background-color: unset;
  margin-bottom: 10px;
  outline: unset;
  border: none;
  text-align: left;
  margin-left: 25%;
}

.icon {
  padding: 10px;
  color: rgb(41, 110, 238);
  min-width: 20px;
  border-right: rgb(41, 110, 238) solid 5px;

}

.input-field {
  text-align: center;
  outline: unset;
  border: none; 
  width: 17%;
  padding: 10px;
  padding-left: 20px;
  text-align: center;
  border-radius: 30px;
}
.input-field::placeholder {
  color: transparent;
}
.input-field:focus {
  width: 40%;
  outline: unset;
  border: none;
  border-top-right-radius:15px ;
  border-bottom-right-radius: 15px;
  text-align: left;
  padding-left: 60px ;
}
.input-field:focus::placeholder{
  color: rgb(68, 97, 215);
}

h1 {
  text-align: center;
  background-color: rgba(255, 255, 255, 0.95);
  margin: 1.5vw 5vw;
  color: darkblue;
  padding: 30px 0;
  border-radius: 10px;
}

div {
  text-align: center;
}